.animate-route {
    animation-duration: 1s;
    animation-fill-mode: both;

    animation: fadeRoute .2s linear;
}

@keyframes fadeRoute {
    from {
        left: -100%;
        opacity: 0;
    }

    to {
        left: 0;
        opacity: 1;
    }
}

@keyframes loginBtnBg {
    0% {
        left: 0%;
        opacity: 0;
    }

    33% {
        // transform: scale(1.1, 1.1);
        left: 13%;
        opacity: .8;
    }

    66% {
        // transform: scale(1.3, 1.3);
        left: 29%;
        opacity: 1;
    }

    100% {
        // transform: scale(1.1, 1.1);
        left: 40%;
        opacity: 0;
    }
}