@import "../../../Common/commonStyle.less";


.reset-box {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    &-title {
        color: rgba(255, 255, 255, 1);
        font-family: @PingFang-SC-Bold-fontFamily;
        font-weight: bold;
    }

    .reset-form {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        background-image: url('../../../Assets/account/create@2x.png');
        background-size: 100% 100%;
        background-repeat: no-repeat;
        .reset-return {
            position: absolute;
            cursor: pointer;
        }

        &-title {
            font-family: @PingFang-SC-Medium-fontFamily;
            font-weight: 500;
            color: rgba(255, 255, 255, 1);
        }

        &-account {
            border-radius: 4px;

            .ant-input-group-addon .ant-select.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {

                background-color: #fff;
            }

            .ant-input-group-addon {
                border-color: #fff !important;
                padding-right: 0 !important;
                overflow: hidden;
            }

            .ant-select-selector {
                padding: 0 !important;

            }

            .ant-input {
                border-left: none !important;
                border-color: #fff !important;

                &:hover {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }

                &:focus {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }
            }
        }

        .get-captcha-bnt {
            position: relative;
            cursor: pointer;
            background: #fff;
            padding: 0 8px;
            font-family: PingFang-SC-Medium, PingFang-SC;
            font-weight: 500;
            color: rgba(64, 160, 251, 1);
        }
        &-input {
            .ant-input {
                border-left: none !important;
                border-color: #fff !important;
                border-radius: 4px !important;

                &:hover {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }

                &:focus {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }
            }

        }

        .get-captcha-box {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            .get-btn {
                padding: 10px 0;
                color: rgba(255, 255, 255, 1);
                cursor: pointer;
            }
        }


        .reset-btn {
            position: relative;
            background-color: rgba(255, 255, 255, 0);
            border: none;
            background-image: url(../../../Assets/account/btn-border@2x.png);
            background-size: 100% 100%;
            outline: none;
            text-decoration: none;
            color: #fff;
            overflow: hidden;
            cursor: pointer;

            img {
                position: absolute;
                left: 0;
                top: 0;
                width: 60%;
                height: 100%;
            }
        }

        .reset-mode {
            p {
                font-family: @PingFang-SC-Medium-fontFamily;
                color: rgba(255, 255, 255, 1);
            }

            // div {
            //     cursor: pointer;

            //     span {
            //         font-family: @PingFangSC-Regular-fontFamily;
            //         color: rgba(100, 223, 222, 1);
            //     }
            // }
        }
    }
}


@media screen and(min-width: 1025px)and(max-width: 1440px) {
    .reset-box {
        margin-left: -180px;
        &-title {
            font-size: 27px;
            line-height: 38px;
            text-shadow: 0px 2px 17px rgba(19, 21, 44, 0.52);
            margin-bottom: 17px !important;
        }

        .reset-form {
            width: 360px;
            .ant-form-item {
                margin-bottom: 8px !important;
            }

            .reset-return {
                left: 21px;
                top: 22px;
                width: 30px;
                height: 30px;

                &:last-of-type {
                    fill: rgba(100, 223, 222, .8);
                }
            }

            &-title {
                padding-top: 41px;
                margin-bottom: 18px;
                font-size: 15px;
                color: rgba(255, 255, 255, 1);
                line-height: 21px;
            }

            .reset-btn {
                width: 236px;
                height: 35px;
                font-size: 12px;
                font-weight: 400;
                line-height: 17px;
                margin-top: 10px;
            }

            &-account {
                width: 236px;
                .ant-input-group-addon {
                    background-color: #fff;
                    padding: 3px !important;

                }
            }

            .get-captcha-box {
                .get-btn {
                    font-size: 10px;
                    line-height: 14px;
                    width: 55%;
                }
            }

            .get-captcha-bnt {
                font-size: 12px;
                line-height: 22px;

                width: 70px;
                &::after {
                    content: '';
                    width: 1px;
                    height: 14px;
                    background-color: rgba(229, 229, 229, 1);
                    position: absolute;
                    left: -2px;
                    top: 4px;

                }

            }

            .reset-mode {
                margin-top: 15px;
                margin-bottom: 50px;
                p {
                    font-size: 12px;
                    font-weight: 500;
                    line-height: 17px;

                }

                

            }

        }
    }
}

@media screen and(min-width: 1441px)and(max-width: 1920px) {
    .reset-box {
        margin-left: -240px;
        &-title {
            font-size: 36px;
            line-height: 50px;
            text-shadow: 0px 2px 22px rgba(19, 21, 44, 0.52);
            margin-bottom: 22px !important;

        }

        .reset-form {
            width: 480px;

            .ant-form-item {
                margin-bottom: 10px !important;
            }
            .reset-return {
                left: 30px;
                top: 30px;
                height: 40px;
                width: 40px;

                &:last-of-type {
                    fill: rgba(100, 223, 222, 1);
                }
            }

            &-title {
                padding-top: 54px;
                margin-bottom: 24px;
                font-size: 20px;
                color: rgba(255, 255, 255, 1);
                line-height: 28px;
            }

            &-account {
                width: 315px;
                .ant-input {
                    font-size: 16px;
                    padding: 10px;
                }

                .ant-input-group-addon {
                    padding: 10px !important;
                    background-color: #fff;

                }
            }

            &-input {
                .ant-input {
                    font-size: 16px;
                    padding: 10px;
                }

            }

            .get-captcha-box {
                .get-btn {
                    width: 40%;
                }
            }

            .get-captcha-bnt {
                font-size: 16px;
                line-height: 22px;

                width: 100px;
                &::after {
                    content: '';
                    width: 2px;
                    height: 18px;
                    background-color: rgba(229, 229, 229, 1);
                    position: absolute;
                    left: -4px;
                    top: 2px;

                }

            }

            .reset-btn {
                width: 315px;
                height: 50px;
                font-size: 16px;
                font-weight: 400;
                line-height: 22px;
                margin-top: 12px;
            }

            .reset-mode {
                margin-top: 20px;
                margin-bottom: 67px;
                p {

                    font-size: 16px;
                    line-height: 22px;
                    font-weight: 500;

                }


            }

        }

    }
}