@import "../../../Common/commonStyle.less";

.form-box {
    margin-top: 5%;
    width: 23.75vw;
    padding: 30px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 16px 2px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}

.login-box {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;

    .ant-form-item {
        margin-bottom: 20px !important;
    }


    &-title {
        color: rgba(255, 255, 255, 1);
        font-family: @PingFang-SC-Bold-fontFamily;
        font-weight: bold;
    }

    .login-form {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        background-image: url('../../../Assets/account/login-bg@2x.png');
        background-size: 100% 100%;
        background-repeat: no-repeat;

        .reset-return {
            position: absolute;
            cursor: pointer;
        }

        &-title {
            font-family: @PingFang-SC-Medium-fontFamily;
            font-weight: 500;
            color: rgba(255, 255, 255, 1);
        }

        &-account {
            border-radius: 4px;

            .ant-input-group-addon .ant-select.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {

                background-color: #fff;
            }

            .ant-input-group-addon {
                border-color: #fff !important;
                padding-right: 0 !important;
                overflow: hidden;
            }

            .ant-select-selector {
                padding: 0 !important;

            }

            .ant-input {
                border-left: none !important;
                border-color: #fff !important;

                &:hover {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }

                &:focus {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }
            }
        }

        &-input {
            .ant-input-password {
                border-left: none !important;
                border-color: #fff !important;
                border-radius: 4px !important;

                &:hover {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }

                &:focus {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }
            }

        }

        .login-btn {
            position: relative;
            background-color: rgba(255, 255, 255, 0);
            border: none;
            background-image: url(../../../Assets/account/btn-border@2x.png);
            background-size: 100% 100%;
            outline: none;
            text-decoration: none;
            color: #fff;
            overflow: hidden;
            cursor: pointer;

            img {
                position: absolute;
                left: 0;
                top: 0;
                width: 70%;
                height: 100%;
            }
        }

        .login-mode {
            p {
                font-family: @PingFang-SC-Medium-fontFamily;
                color: rgba(255, 255, 255, 1);
            }

            div {
                cursor: pointer;

                span {
                    font-family: @PingFangSC-Regular-fontFamily;
                    color: rgba(100, 223, 222, 1);
                }
            }
        }
    }
}



@media screen and(min-width: 1025px)and(max-width: 1440px) {
    .login-box {
        margin-left: -180px;

        &-title {
            font-size: 27px;
            line-height: 38px;
            text-shadow: 0px 2px 17px rgba(19, 21, 44, 0.52);
            margin-bottom: 17px !important;
        }

        .login-form {
            width: 360px;

            .ant-form-item {
                margin-bottom: 8px !important;
            }
            .reset-return {
                left: 21px;
                top: 22px;
                width: 30px;
                height: 30px;

                &:last-of-type {
                    fill: rgba(100, 223, 222, .8);
                }
            }

            &-title {
                margin-top: 41px;
                margin-bottom: 18px;
                font-size: 20px;
                color: rgba(255, 255, 255, 1);
                line-height: 28px;
            }

            .login-btn {
                width: 236px;
                height: 35px;
                font-size: 12px;
                font-weight: 400;
                line-height: 17px;
                margin-top: 7px;
            }

            &-account {
                .ant-input-group-addon {
                    padding: 3px !important;

                }
            }

            .login-mode {
                margin-top: 23px;
                margin-bottom: 33px;
                p {
                    font-size: 12px;
                    font-weight: 500;
                    line-height: 17px;

                }

            }

        }
    }
}

@media screen and(min-width: 1441px)and(max-width: 1920px) {
    .login-box {
        margin-left: -240px;

        &-title {

            font-size: 36px;
            line-height: 50px;
            text-shadow: 0px 2px 22px rgba(19, 21, 44, 0.52);
            margin-bottom: 22px !important;

        }

        .login-form {
            width: 480px;

            .ant-form-item {
                margin-bottom: 10px !important;
            }

            .reset-return {
                left: 30px;
                top: 30px;
                height: 40px;
                width: 40px;

                &:last-of-type {
                    fill: rgba(100, 223, 222, 1);
                }
            }

            &-title {
                margin-top: 54px;
                margin-bottom: 24px;
                font-size: 20px;
                color: rgba(255, 255, 255, 1);
                line-height: 28px;
            }

            &-account {
                .ant-input {
                    font-size: 16px;
                    padding: 9px;
                }

                .ant-input-group-addon {
                    padding: 9px !important;
                    background-color: #fff;

                }
            }

            &-input {
                .ant-input-password {
                    font-size: 16px;
                    padding: 9px;
                }

            }

            .login-btn {
                width: 315px;
                height: 45px;
                font-size: 16px;
                font-weight: 400;
                line-height: 22px;
                margin-top: 10px;
            }

            .login-mode {
                margin-top: 20px;
                margin-bottom: 45px;
                p {

                    font-size: 16px;
                    line-height: 22px;
                    font-weight: 500;

                }

            }

        }

    }
}