@import "../../../Common/commonStyle.less";


.verify-box {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;


    &-title {
        color: rgba(255, 255, 255, 1);
        font-family: @PingFang-SC-Bold-fontFamily;
        font-weight: bold;
    }

    .verify-form {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        background-image: url('../../../Assets/account/login-bg@2x.png');
        background-size: 100% 100%;
        background-repeat: no-repeat;

        &-title {
            font-family: @PingFang-SC-Medium-fontFamily;
            font-weight: 500;
            color: rgba(255, 255, 255, 1);
        }

        &-account {
            border-radius: 4px;

            .ant-input-group-addon .ant-select.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {

                background-color: #fff;
            }

            .ant-input-group-addon {
                border-color: #fff !important;
                padding-right: 0 !important;
                overflow: hidden;
            }

            .ant-select-selector {
                padding: 0 !important;

            }

            .ant-input {
                border-left: none !important;
                border-color: #fff !important;

                &:hover {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }

                &:focus {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }
            }
        }

        .verify-btn {
            position: relative;
            background-color: rgba(255, 255, 255, 0);
            border: none;
            background-image: url(../../../Assets/account/btn-border@2x.png);
            background-size: 100% 100%;
            outline: none;
            text-decoration: none;
            color: #fff;
            overflow: hidden;
            cursor: pointer;

            img {
                position: absolute;
                left: 0;
                top: 0;
                width: 70%;
                height: 100%;
                // animation: loginBtnBg 3s linear infinite alternate;
            }
        }

        .verify-mode {
            p {
                font-family: @PingFang-SC-Medium-fontFamily;
                color: rgba(255, 255, 255, 1);
            }

            div {
                cursor: pointer;

                span {
                    font-family: @PingFangSC-Regular-fontFamily;
                    color: rgba(100, 223, 222, 1);
                }
            }
        }
    }
}



@media screen and(min-width: 1025px)and(max-width: 1440px) {
    .verify-box {
        margin-left: -180px;

        &-title {
            font-size: 27px;
            line-height: 38px;
            text-shadow: 0px 2px 17px rgba(19, 21, 44, 0.52);
            margin-bottom: 13px !important;
        }

        .verify-form {
            width: 360px;

            .ant-form-item {
                margin-bottom: 14px !important;
            }

            &-title {
                margin-top: 38px;
                margin-bottom: 20px;
                font-size: 20px;
                color: rgba(255, 255, 255, 1);
                line-height: 28px;
            }

            .verify-btn {
                width: 236px;
                height: 35px;
                font-size: 12px;
                font-weight: 400;
                line-height: 17px;
            }

            &-account {
                .ant-input-group-addon {
                    padding: 3px !important;

                }
            }

            .verify-mode {
                margin-top: 13px;
                margin-bottom: 10px;

                p {
                    font-size: 12px;
                    font-weight: 500;
                    line-height: 17px;

                }

                div {
                    margin-top: 10px;

                    span {
                        font-size: 11px;
                        font-weight: 400;
                        line-height: 15px;
                        position: relative;
                        left: -4px;

                    }

                    .wechat {
                        position: relative;
                        left: -2px;
                        width: 36px;
                        height: 36px;
                    }

                }

            }

        }
    }
}

@media screen and(min-width: 1441px)and(max-width: 1920px) {
    .verify-box {
        margin-left: -240px;

        &-title {

            font-size: 36px;
            line-height: 50px;
            text-shadow: 0px 2px 22px rgba(19, 21, 44, 0.52);
            margin-bottom: 17px !important;
        }

        .verify-form {
            width: 480px;

            .ant-form-item {
                margin-bottom: 19px !important;
            }

            &-title {
                padding-top: 50px;
                margin-bottom: 27px;
                font-size: 20px;
                color: rgba(255, 255, 255, 1);
                line-height: 28px;
            }

            &-account {
                .ant-input {
                    font-size: 16px;
                    padding: 10px;
                }

                .ant-input-group-addon {
                    padding: 10px !important;
                    background-color: #fff;

                }
            }

            .verify-btn {
                width: 315px;
                height: 50px;
                font-size: 16px;
                font-weight: 400;
                line-height: 22px;
            }

            .verify-mode {
                margin-top: 17px;
                margin-bottom: 20px;

                p {
                    font-size: 16px;
                    line-height: 22px;
                    font-weight: 500;
                }

                div {
                    margin-top: 15px;

                    span {
                        position: relative;
                        left: -2px;
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 20px;

                    }

                    .wechat {
                        position: relative;
                        left: -8px;
                        width: 42px;
                        height: 42px;
                    }

                }

            }

        }

    }
}